import Vue from "vue";
import VueRouter from "vue-router";
import Profile from "../views/Profile.vue";
import Portfolio from "../views/Portfolio.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Profile",
    component: Profile
  },
  {
    path: "/portfolio",
    name: "Portfolio",
    component: Portfolio
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
